<template>
  <div class="start-family-view">
    <video
      ref="videoPlayer"
      class="background-video"
      autoplay
      loop
      muted
      playsinline
    >
      <source :src="videoSrc" type="video/mp4">
    </video>
    
    <StarterPanel 
      @game-start="handleGameStart" 
      @toggle-bluetooth="toggleBluetooth"
      :is-connected="isConnected"
      :is-connecting="isConnecting"
      :bluetooth-error="bluetoothError"
    />
    <BuzzCompetition 
      v-model="showBuzz"
      :team1Name="team1Name"
      :team2Name="team2Name"
      :bluetooth-characteristic="bluetoothCharacteristic"
      :showKeyboardHints="gameConfig?.showKeyboardHints"
      @team-selected="handleTeamSelected"
    />
    
    <!-- Dialog z QR kodem do panelu hosta -->
    <v-dialog
      v-model="showHostPanelDialog"
      persistent
      max-width="500"
    >
      <v-card class="text-center pa-4" style="background-color: #000; background-image: radial-gradient(#333 2px, transparent 3px); background-size: 10px 10px;">
        <v-card-title class="text-h4 mb-4" style="color: yellow; font-family: 'PixelFont';">
          Panel Hosta
        </v-card-title>
        
        <v-card-text>
          <p class="mb-4" style="color: yellow; font-family: 'PixelFont';">
            Zeskanuj kod QR lub otwrz link na telefonie:
          </p>
          
          <div v-if="hostPanelUrl" class="mb-4 d-flex justify-center">
            <div style="background-color: white; padding: 16px; border-radius: 8px; display: inline-block;">
              <qrcode-vue :value="hostPanelUrl" :size="300" level="H" render-as="svg" />
            </div>
          </div>
          <div v-else class="mb-4">
            <v-progress-circular indeterminate color="yellow" size="64"></v-progress-circular>
          </div>
          
          <div class="mb-4">
            <a
              v-if="hostPanelUrl"
              :href="hostPanelUrl"
              target="_blank"
              style="color: yellow; text-decoration: underline; word-break: break-all; font-family: 'PixelFont';"
            >
              {{ hostPanelUrl }}
            </a>
          </div>
        </v-card-text>
        
        <v-card-actions class="justify-center pa-4">
          <v-btn
            color="yellow"
            @click="goToGame"
            size="large"
            style="font-family: 'PixelFont';"
          >
            Rozpocznij gr
          </v-btn>
          <v-btn
            color="grey"
            @click="showHostPanelDialog = false"
            size="large"
            style="font-family: 'PixelFont';"
          >
            Zamknij
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import StarterPanel from '@/components/Family/StarterPanel.vue';
import BuzzCompetition from '@/components/Family/BuzzCompetition.vue';
import backgroundVideo from '@/assets/video/background.mp4'
import { useAudioManager } from '@/composables/useAudioManager';
import { createFamilyGameSession } from '@/firebase/familyGameSession';
import QrcodeVue from 'qrcode.vue';

export default defineComponent({
  name: 'StartFamilyView',
  components: {
    StarterPanel,
    BuzzCompetition,
    QrcodeVue
  },
  data() {
    return {
      videoSrc: backgroundVideo,
      bluetoothDevice: null,
      bluetoothCharacteristic: null,
      isConnected: false,
      showBuzz: false,
      team1Name: '',
      team2Name: '',
      bluetoothError: null,
      isConnecting: false,
      gameConfig: null,
      sessionId: null,
      showHostPanelDialog: false
    }
  },
  computed: {
    hostPanelUrl() {
      if (!this.sessionId) return '';
      // Uyj aktualnego base path zamiast hardcodowanego
      const basePath = window.location.pathname.replace(/\/$/, '') || '';
      return `${window.location.origin}${basePath}/#/family/host/${this.sessionId}`;
    }
  },
  setup() {
    const audioManager = useAudioManager();
    return { audioManager };
  },
  methods: {
    async toggleBluetooth() {
      if (this.isConnected) {
        await this.disconnectBluetooth();
      } else {
        await this.connectToBluetooth();
      }
    },
    async connectToBluetooth() {
      this.isConnecting = true;
      this.bluetoothError = null;

      const options = {
        filters: [{ 
          services: ['0000ffe0-0000-1000-8000-00805f9b34fb']
        }],
        optionalServices: ['0000ffe0-0000-1000-8000-00805f9b34fb']
      };

      try {
        this.bluetoothDevice = await navigator.bluetooth.requestDevice(options);
        const server = await this.bluetoothDevice.gatt.connect();
        const service = await server.getPrimaryService('0000ffe0-0000-1000-8000-00805f9b34fb');
        this.bluetoothCharacteristic = await service.getCharacteristic('0000ffe1-0000-1000-8000-00805f9b34fb');
        await this.bluetoothCharacteristic.startNotifications();

        this.bluetoothCharacteristic.addEventListener('characteristicvaluechanged', (event) => {
          const value = event.target.value.getUint8(0);
          if (value === 49 || value === 50) {
            this.showBuzz = true;
            this.handleTeamSelected(value === 49 ? 1 : 2);
          }
        });
        
        this.isConnected = true;
        this.bluetoothError = null;

        this.bluetoothDevice.addEventListener('gattserverdisconnected', () => {
          this.handleDisconnection();
        });

      } catch (error) {
        this.bluetoothError = this.getBluetoothErrorMessage(error);
        this.isConnected = false;
      } finally {
        this.isConnecting = false;
      }
    },

    getBluetoothErrorMessage(error) {
      if (error.name === 'NotFoundError') {
        return 'Nie wybrano urzdzenia. Sprbuj ponownie i wybierz HM-10.';
      } else if (error.name === 'SecurityError') {
        return 'Brak uprawnie do Bluetooth. Sprawd ustawienia przegldarki.';
      } else if (error.name === 'NotSupportedError') {
        return 'Twoja przegldarka nie wspiera Web Bluetooth API.';
      } else {
        return `Bd poczenia: ${error.message}`;
      }
    },

    handleDisconnection() {
      this.isConnected = false;
      this.bluetoothDevice = null;
      this.bluetoothCharacteristic = null;
      this.bluetoothError = 'Urzdzenie zostao rozczone';
    },

    async disconnectBluetooth() {
      if (this.bluetoothCharacteristic) {
        try {
          await this.bluetoothCharacteristic.stopNotifications();
        } catch (error) {
          console.error("Error stopping notifications", error);
        }
      }

      if (this.bluetoothDevice && this.bluetoothDevice.gatt.connected) {
        await this.bluetoothDevice.gatt.disconnect();
      }

      this.handleDisconnection();
    },

    async handleGameStart(config) {
      console.log('Otrzymana konfiguracja:', config);
      
      if (!config.questionsFile) {
        console.error('Brak pliku z pytaniami');
        alert('Brak pliku z pytaniami');
        return;
      }

      try {
        this.audioManager.stopAll();
        
        // Czytamy zawarto pliku
        const reader = new FileReader();
        
        const fileContent = await new Promise((resolve, reject) => {
          reader.onload = (event) => {
            console.log('Plik wczytany pomylnie');
            resolve(event.target.result);
          };
          reader.onerror = () => {
            console.error('Bd podczas czytania pliku');
            reject(new Error('Bd odczytu pliku'));
          };
          reader.readAsText(config.questionsFile);
        });

        const questionsData = JSON.parse(fileContent);
        
        if (!questionsData.questions || !Array.isArray(questionsData.questions)) {
          throw new Error('Nieprawidowy format pliku z pytaniami');
        }

        // Jeli wczono losow kolejno, mieszamy pytania
        if (config.randomizeQuestions) {
          questionsData.questions = this.shuffleArray([...questionsData.questions]);
        }

        // Przygotuj konfiguracj do zapisania
        const fullConfig = {
          team1Name: config.team1Name,
          team2Name: config.team2Name,
          questionsData: questionsData.questions,
          randomizeQuestions: config.randomizeQuestions,
          autoIncreaseMultiplier: config.autoIncreaseMultiplier,
          isBluetoothConnected: this.isConnected,
          gameEndCondition: config.gameEndCondition,
          gameEndLimit: config.gameEndLimit,
          showKeyboardHints: config.showKeyboardHints
        };

        // Utwrz sesj Firebase
        this.sessionId = await createFamilyGameSession(fullConfig);
        fullConfig.sessionId = this.sessionId;

        // Zapisz konfiguracj z sessionId
        localStorage.setItem('familyGameConfig', JSON.stringify(fullConfig));
        localStorage.setItem('familyGameSessionId', this.sessionId);

        // Zapisz stan Bluetooth
        if (this.bluetoothDevice) {
          window.bluetoothState = {
            device: this.bluetoothDevice,
            characteristic: this.bluetoothCharacteristic,
            isConnected: this.isConnected,
            server: this.bluetoothDevice?.gatt
          };
          this.bluetoothDevice.removeEventListener('gattserverdisconnected', this.handleDisconnection);
        }

        // Poka dialog z QR kodem do panelu hosta
        this.showHostPanelDialog = true;
        
      } catch (error) {
        console.error('Szczegowy bd:', error);
        alert(`Bd podczas uruchamiania gry: ${error.message}`);
      }
    },

    handleTeamSelected(team) {
      if (team === 1) {
        this.team1Name = 'Druyna 1';
      } else if (team === 2) {
        this.team2Name = 'Druyna 2';
      }
      
      setTimeout(() => {
        this.showBuzz = false;
      }, 3000);
    },

    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    },
    goToGame() {
      this.showHostPanelDialog = false;
      // Zmiana sposobu nawigacji
      try {
        this.$router.replace('/family');
      } catch (navigationError) {
        console.error('Bd nawigacji:', navigationError);
        // Sprbuj alternatywn metod
        window.location.href = '/family';
      }
    }
  },
  beforeUnmount() {
    // Zatrzymaj odtwarzanie dwiku przed usuniciem komponentu
    this.audioManager.stopAll();
    
    // Usuwamy automatyczne rozczanie przy przejciu do nastpnego widoku
    if (this.$router.currentRoute.value.path === '/family') {
      return;
    }
    this.disconnectBluetooth();
  },
  mounted() {
    // Odtwarzamy dwik intro po zaadowaniu komponentu
    this.audioManager.playIntro();
  }
});
</script>

<style scoped>
.start-family-view {
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.background-video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 1;
  object-fit: cover;
  transform: translateX(0) translateY(0);
}
</style> 