import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import FamilyView from '@/views/Family/FamilyView.vue'
import StartFamilyView from '@/views/Family/StartFamilyView.vue'
import HostPanelView from '@/views/Family/HostPanelView.vue'
import BoardView from '@/views/Politics/BoardView.vue'
import PoliticsStartView from '@/views/Politics/PoliticsStartView.vue'
import PoliticianPanelView from '@/views/Politics/PoliticianPanelView.vue'
import JoinGameView from '@/views/Politics/JoinGameView.vue'
import PoliticsSettingsView from '@/views/Politics/PoliticsSettingsView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/family',
    name: 'family',
    component: FamilyView,
    beforeEnter: (to, from, next) => {
      // Sprawd czy mamy konfiguracj w localStorage
      const config = localStorage.getItem('familyGameConfig');
      if (config) {
        next(); // Pozwl przej jeli jest konfiguracja
      } else {
        next('/start-family'); // Przekieruj jeli nie ma konfiguracji
      }
    }
  },
  {
    path: '/start-family',
    name: 'start-family',
    component: StartFamilyView
  },
  {
    path: '/family/host',
    name: 'family-host',
    component: HostPanelView,
    beforeEnter: (to, from, next) => {
      // Sprawd czy mamy konfiguracj w localStorage lub sessionId w URL
      const config = localStorage.getItem('familyGameConfig');
      const sessionId = to.params.sessionId || to.query.sessionId;
      
      if (config || sessionId) {
        next(); // Pozwl przej jeli jest konfiguracja lub sessionId
      } else {
        next('/start-family'); // Przekieruj jeli nie ma konfiguracji
      }
    }
  },
  {
    path: '/family/host/:sessionId',
    name: 'family-host-session',
    component: HostPanelView,
    beforeEnter: (to, from, next) => {
      // Zapisz sessionId w localStorage, jeli nie ma konfiguracji
      const config = localStorage.getItem('familyGameConfig');
      if (!config && to.params.sessionId) {
        localStorage.setItem('familyGameSessionId', to.params.sessionId);
      }
      next();
    }
  },
  {
    path: '/',
    redirect: '/start-family'
  },
  {
    path: '/politics',
    name: 'politics-start',
    component: PoliticsStartView
  },
  {
    path: '/politics/board/:sessionId',
    name: 'politics-board',
    component: BoardView,
    props: true
  },
  {
    path: '/join-game/:sessionId',
    name: 'join-game',
    component: JoinGameView,
    props: true
  },
  {
    path: '/politician-panel/:sessionId/:deputyName',
    name: 'PoliticianPanel',
    component: PoliticianPanelView,
    props: true
  },
  {
    path: '/politics/settings',
    name: 'politics-settings',
    component: PoliticsSettingsView
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

// Dodaj globalny guard dla bezporedniego dostpu do /family
router.beforeEach((to, from, next) => {
  if (to.path === '/family' && from.path === '/') {
    next('/start-family');
  } else {
    next();
  }
});

export default router
